.structure {
  display: flex;
  flex-direction: column;
  gap: 5px;
  height: max-content;
  width: calc(320px - 10px);
  padding: 50px 5px 50px 5px;
}
.heading {
  width: 100%;
  text-align: center;
  color: var(--blue);
  box-shadow: 1px 1px 8px var(--grey);
  border-radius: 5px;
  padding: 10px 0px;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 5px 0px;
}
.total {
  display: flex;
  justify-content: space-between;
  width: calc(100% - 10px);
  text-align: center;
  color: var(--white);
  background-color: var(--darkBlue);
  border-radius: 5px;
  font-size: 1rem;
  padding: 10px 5px;
  font-weight: 600;
  margin: 5px 0px;
}
