.modal {
  display: block;
  position: absolute;
  min-height: calc(100% - 200px);
  z-index: 1;
  width: 320px;
  margin: 100px auto;
}
.background {
  position: fixed;
  top: 0px;
  z-index: -1;
  width: 320px;
  height: 100%;
  display: block;
  background: rgba(129, 129, 129, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3.8px);
  -webkit-backdrop-filter: blur(3.8px);
}
.card {
  display: flex;
  height: max-content;
  width: 280px;
  border-radius: 10px;
  background-color: var(--white);
  position: relative;
  top: 25%;
  margin: auto;
  box-shadow: 0px 0px 12px var(--blue);
}
.form {
  display: flex;
  padding: 10px;
  margin: 5px;
  flex-direction: column;
  gap: 5px;
  width: calc(100% - 30px);
  border-radius: 5px;
}
.form span {
  margin: 3px auto;
  text-align: center;
  font-size: 1.2rem;
  color: var(--blue);
  font-weight: 600;
  width: 50%;
  border-radius: 10px;
  padding: 5px;
  box-shadow: 3px 3px 8px var(--grey);
}
.form label {
  width: 100%;
  text-align: center;
  margin: 5px 0px;
  padding: 5px 0px;
  font-size: 1.1rem;
  font-weight: 500;
}
.form input {
  margin: 5px 0px;
  font: 1.1rem;
  padding: 10px;
  outline: none;
  border: none;
  box-shadow: 5px 5px 10px var(--lightBlue);
  border-radius: 10px;
}
.form button{
  width: 50%;
  margin: auto;
  background-color: var(--blue);
  outline: none;
  border: none;
  box-shadow: 2px 2px 8px var(--grey);
  color: var(--white);
  padding: 10px;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
}
.form .err{
  all: unset;
  font-size: 1rem;
  color: var(--red);
  margin: auto;
  width: 50%;
  padding: 5px;
}
.form .err::before{
  content: "*";
}