.heading{
    width: 100%;
    padding: 10px 0px;
    text-align: center;
    color: var(--blue);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 10px 0px;
}
.box{
    width: calc(100% - 20px);
    margin: 10px 0px;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    box-shadow: 5px 5px 10px var(--grey);
}
.box .desc{
    font-size: 1rem;
}
.box .amount{
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 8px;
    font-size: 1rem;
}
.amou{
    font-size: 1.2rem;
    font-weight: 600;
}
.amou::before{
    content: "₹";
}
.date{
    font-size: 0.7rem;
    color: var(--blue);
}
.date::before{
    content: "Date:";
}
.warning{
    text-align: center;
    color: var(--red);
}