.form {
    display: flex;
    flex-direction: column;
    padding: 5px;
    border-radius: 10px;
    min-height: 200px;
    box-shadow: 5px 5px 10px var(--grey);
  }
  .form div {
    width: 100%;
    text-align: center;
    color: var(--blue);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 5px 0px;
  }
  .form span {
    color: var(--red);
    font-size: 0.7rem;
    font-weight: 600;
  }
  .form span::before{
      content: "⚠️";
  }
  .form label {
    width: 100%;
    font-size: 1rem;
    margin: 5px 0px;
  }
  .form label input {
    outline: none;
    width: calc(100% - 20px);
    padding: 10px 10px;
    font-size: 1rem;
    border-radius: 5px;
    border: none;
    margin: 5px 0px;
    background-color: var(--white);
  }
  .form button {
    width: 100px;
    background-color: var(--darkBlue);
    color: var(--white);
    padding: 10px 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 600;
    font-size: 1rem;
    margin: auto;
  }