.headercontainer {
  width: 320px;
  position: fixed;
  top: 0;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  background-color: var(--blue);
  color: var(--white);
  min-height: 40px;
}
.btn {
  background-color: var(--darkBlue);
  color: var(--white);
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
}
.btn:hover {
  background-color: var(--black);
}
.heading {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 5px;
}
.sidescreen {
  display: none;
  width: 100px;
  background-color: var(--blue);
  height: calc(100vh - 40px);
  position: fixed;
}

.sidescreen ul {
  padding: 5px;
  margin: 0px;
}
.sidescreen li {
  margin: 0px 0px 4px 0px;
  list-style-type: none;
}
.sidescreen li button {
  background-color: var(--darkBlue);
  width: 100%;
  color: var(--white);
  padding: 10px 5px;
  font-size: 0.9rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
}
.sidescreen li button:hover {
  background-color: var(--black);
}
.appear {
  display: block;
  animation: slide-in-left 0.2s cubic-bezier(0.32, 0.85, 0.68, 1) both;
}
.disappear {
  animation: slide-in-right 0.2s cubic-bezier(0.32, 0.85, 0.68, 1) both;
}
@keyframes slide-in-left {
  0% {
    display: none;
    width: 0px;
    opacity: 0.6;
  }
  100% {
    width: 100px;
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    display: block;
    width: 100px;
    opacity: 1;
  }
  50% {
    display: none;
  }
  100% {
    display: none;
    width: 0px;
    opacity: 0.6;
  }
}
